<template>
    <div class="main">
        <div class="temporaryRingtone">
            <div class="searchA">
                <el-input v-model="keyword" placeholder="请输入铃声名称" clearable size="small"></el-input>
                <el-button size="small" type="primary" plain icon="el-icon-search" @click="serchList()">查询</el-button>
                <div class="add">
                    <el-button size="small" type="primary" icon="el-icon-plus" @click="addBtn">新增临时播放</el-button>
                </div>
            </div>
            <div class="home_table">
                <el-table :data="tableData" border v-loading="isLoading" element-loading-background="rgba(0, 0, 0, 0.4)" :max-height="tableHeight" size="medium" style="width: 100%">
                    <template v-for="(item, index) in tableHead">
                        <el-table-column :prop="item.prop" align="center" v-if="item.prop!='file'" :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true"></el-table-column>
                        <el-table-column :prop="item.prop" align="center" :label="item.title" :key="index" v-else :min-width="item.minWidth" :show-overflow-tooltip='true'>
                            <template slot-scope="scope">
                                <el-link :underline="false" target="_blank" type="primary" :href="baseUrl+scope.row.file_path">{{scope.row.file_name}}</el-link>
                            </template>
                        </el-table-column>
                    </template>
                    <el-table-column prop="address" label="操作" fixed="right" width="180">
                        <template slot-scope="scope">
                            <el-button @click="stop(scope.row,1)" v-if="scope.row.playState==0" type="text" size="small">开始播放</el-button>
                            <el-button @click="stop(scope.row,0)" v-if="scope.row.playState==1||scope.row.playState==3" type="text" size="small" style="color: #ff2b2b">停止播放</el-button>
                            <el-button @click="editBtn(scope.row)" v-if="scope.row.playState==0||scope.row.playState==2" type="text" size="small">编辑</el-button>
                            <el-button @click="delBtn(scope.row.id)" v-if="scope.row.playState==0||scope.row.playState==2||scope.row.playState==4" type="text" size="small" style="color: #ff2b2b">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <div class="pagination">
                <el-pagination background :current-page.sync="page" :page-sizes="[15, 30, 50, 100]" :page-size="pageSize" layout="prev, pager, next, sizes,total" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<15? true:false" />
            </div>
            <el-dialog :title="title" class="dialog" :visible.sync="dialogVisible" v-dialogDrag width="600px" :close-on-click-modal="false" @close='handleClose'>
                <el-form :model="formData" ref="formData" :rules="rule" label-width="110px">
                    <el-form-item label="铃声名称" prop="title">
                        <el-input v-model.trim="formData.title" placeholder="请输入铃声名称"></el-input>
                    </el-form-item>
                    <el-form-item label="播放次数" prop="frequency">
                        <el-input v-model.number="formData.frequency" :disabled='disable' placeholder="请输入播放次数"></el-input>
                    </el-form-item>
                    <el-form-item style="display: block;" label="设备选择">
                        <el-tree :data="nodeData" show-checkbox check-on-click-node node-key="device_id" ref="tree" :props="defaultProps">
                        </el-tree>
                    </el-form-item>
                    <el-form-item label="铃声音乐" prop="file_id">
                        <el-select ref="select" v-model="formData.file_id" filterable clearable @change="fileChange">
                            <el-option v-for="item in fileData" :key="item.id" :label="item.title" :value="item.id"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="铃声预览">
                        <el-link v-if="previewData.title" :underline="false" target="_blank" type="primary" :href='baseUrl + previewData.path'>{{previewData.title}}----{{previewData.duration}}
                        </el-link>
                    </el-form-item>
                    <el-form-item label="预约播放" prop="appoint_time">
                        <el-date-picker v-model="formData.appoint_time" @change="timeChange" format='yyyy-MM-dd HH:mm:ss' value-format='yyyy-MM-dd HH:mm:ss' type="datetime" placeholder="选择日期时间">
                        </el-date-picker>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisible = false">取 消</el-button>
                    <el-button type="primary" @click="submit">确 定</el-button>
                </div>
            </el-dialog>
        </div>
    </div>
</template>
<script>
import {
    temporaryRingtoneView,
    temporaryRingtoneList,
    temporaryRingtoneInfo,
    temporaryRingtoneAdd,
    temporaryRingtoneEdit,
    temporaryRingtoneFileList,
    temporaryRingtoneDel,
    temporaryRingtoneStatus,
    hashApi,
} from '@/api/index';
export default {
    data() {
        return {
            total: 0,
            page: 1,
            pageSize: 15,
            isLoading: false,
            tableHeight: window.innerHeight - 180,
            formData: {
                title: '',
                frequency: '',
                file_id: '',
                appoint_time: '',
            },
            title: '',
            dialogVisible: false,
            rule: {
                title: [
                    { required: true, message: '请输入铃声名称', trigger: 'blur' }
                ],
                frequency: [
                    { required: true, message: '请输入播放次数', trigger: 'blur' },
                    { type: 'number', message: '请输入大于零的整数' }
                ],
                file_id: [
                    { required: true, message: '请选择铃声音乐', trigger: 'change' }
                ]
            },
            tableHead: [
                { title: '序号', prop: 'id', minWidth: '80' },
                { title: '铃声名称', prop: 'title', minWidth: '160' },
                { title: '铃声时长', prop: 'duration', minWidth: '100' },
                { title: '播放次数', prop: 'frequency', minWidth: '80' },
                { title: '铃声预览', prop: 'file', minWidth: '150' },
                { title: '设备选择', prop: 'device_name', minWidth: '100' },
                { title: '创建时间', prop: 'create_time', minWidth: '100' },
                { title: '预约播放', prop: 'appoint_time', minWidth: '100' },
            ],
            tableData: [],
            hash: '',
            listId: '',
            resources: {}, // 权限
            grade_auth: false, // 市局权限
            keyword: '',
            baseUrl: '',
            nodeData: [],
            defaultProps: {
                children: 'children',
                label: 'device_name',
            },
            fileData: [],
            loadings: false,
            previewData: {},
            fileID: '',//编辑文件id
            disable: false,
        };
    },
    mounted() {
        this.baseUrl = process.env.UPLOADER_URL;
        this.getList();
        this.getHash();
        this.resView();
    },
    methods: {
        timeChange(e) {
            console.log(e === null);
            if (e !== null) {
                this.disable = true;
                this.formData.frequency = 1;
            } else {
                this.disable = false;
            }
        },
        serchList() {
            this.page = 1;
            this.pageSize = 15;
            this.getList()
        },
        // 监听 pagesize 改变
        handleSizeChange(newSize) {
            this.page = 1;
            this.pageSize = newSize;
            this.getList();
        },
        // 监听 pagenum 改变
        handleCurrentChange(newPage) {
            this.page = newPage;
            this.getList();
        },
        fileChange() {
            if (String(this.formData.file_id).length > 0) {
                this.fileData.forEach(item => {
                    if (item.id == this.formData.file_id) {
                        this.previewData = {
                            title: item.title,
                            path: item.file_path,
                            duration: item.duration,
                        }
                    }
                })
            }
        },
        // async requestMethod(query) {
        //     this.loadings = true;
        //     let data = {
        //         title: query,
        //     }
        //     if (String(this.fileID).length > 0) {
        //         data.id = this.fileID
        //     }
        //     const res = await this.axios.post(temporaryRingtoneFileList, data);
        //     if (res.data.code == 1) {
        //         this.fileData = res.data.data.file_list;
        //         this.fileID = '';
        //         delete data.id;
        //     } else {
        //         this.$message({
        //             showClose: true,
        //             message: res.data.msg,
        //             type: 'error'
        //         });
        //     }
        // },
        //获取Hash
        async getHash() {
            const res = await this.axios.post(hashApi, { page_hash: 'temporaryRingtone' });
            if (res.data.code == 1) {
                this.hash = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        async getNodeData(e) {
            this.nodeData = [];
            const res = await this.axios.post(temporaryRingtoneView)
            if (res.data.code == 1) {
                this.nodeData = res.data.data.device_list;
                if (e) {
                    this.$nextTick(function () {
                        this.$refs.tree.setCheckedKeys([]);
                        e.forEach((i) => {
                            var node = this.$refs.tree.getNode(i);
                            if (node) {
                                this.$refs.tree.setChecked(node, true);
                            }
                        });
                    })
                } else {
                    // 获取所有id 赋值树节点
                    const arr = [];
                    this.nodeData.forEach(data => {
                        arr.push(data.device_id)
                        if (data.children) {
                            data.children.forEach(item => {
                                arr.push(item.device_id)
                            })
                        }
                    });
                    this.$nextTick(function () {
                        this.$refs.tree.setCheckedKeys(arr);
                    })
                }

            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: "error"
                });
            }
        },
        async resView() {
            const res = await this.axios.post(temporaryRingtoneView);
            if (res.data.code == 1) {
                this.nodeData = res.data.data.device_list;
                this.fileData = res.data.data.file_list;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }

        },
        async getList() {
            let data = {
                curr: this.page,
                pageSize: this.pageSize,
                keyword: this.keyword,
            }
            const res = await this.axios.post(temporaryRingtoneList, data);
            if (res.data.code == 1) {
                this.total = res.data.data.total;
                this.tableData = res.data.data.data;
                res.data.data.data.forEach(item => {
                    if (item.appoint_status == 0) {
                        if (item.status == 0) {
                            item.playState = 0;//没有预约时间 未播放
                        } else if (item.status == 1) {
                            item.playState = 1;//没有预约时间 已播放
                        }
                    } else if (item.appoint_status == 1) {
                        item.playState = 2;  //有预约时间 未播放
                    } else if (item.appoint_status == 2) {
                        item.playState = 3;//有预约时间 播放中
                    } else if (item.appoint_status == 3) {
                        item.playState = 4;//有预约时间 播放完毕
                    }
                });
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        //新增播放
        addBtn() {
            this.title = '新增临时铃声';
            this.resView();
            this.dialogVisible = true;
            this.resetForm();
        },
        editBtn(row) {
            this.title = '编辑临时铃声';
            this.dialogVisible = true;
            this.resetForm();
            this.getInfo(row.id);
        },
        resetForm() {
            this.formData = {
                title: '',
                frequency: '',
                file_id: '',
                appoint_time: '',
            };
            this.previewData = {};
            this.$nextTick(() => {
                this.$refs['formData'].resetFields();
            })
        },
        async getInfo(id) {
            const res = await this.axios.post(temporaryRingtoneInfo, { id: id })
            if (res.data.code == 1) {
                this.formData = res.data.data;
                this.previewData = {
                    title: res.data.data.file_data.title,
                    path: res.data.data.file_data.file_path,
                    duration: res.data.data.file_data.duration,
                }
                let nodes = [];
                res.data.data.list.forEach(e => {
                    nodes.push(e.device_id);
                })
                this.getNodeData(nodes)
                this.fileID = res.data.data.file_data.id;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        //提交
        submit() {
            this.$refs['formData'].validate(async valid => {
                if (valid) {
                    await this.getHash();
                    let device_ids = this.$refs.tree.getCheckedKeys().concat(this.$refs.tree.getHalfCheckedKeys());
                    let numReg = /^[0-9]*$/
                    let numRe = new RegExp(numReg)
                    let arr = [];
                    device_ids.forEach(e => {
                        if (numRe.test(e)) {
                            arr.push(e);
                        }
                    })
                    if (this.formData.frequency <= 0) {
                        this.$message({
                            showClose: true,
                            message: '临时播放时次数为正整数',
                            type: 'error'
                        });
                        return
                    }
                    let data = {
                        title: this.formData.title,
                        frequency: this.formData.frequency,
                        file_id: this.formData.file_id,
                        device_ids: arr.join(','),
                        appoint_time: this.formData.appoint_time,
                        hash: this.hash,
                    }
                    let api;
                    if (this.title == '新增临时铃声') {
                        api = temporaryRingtoneAdd;
                    } else {
                        api = temporaryRingtoneEdit;
                        data.id = this.formData.id;
                    }
                    const res = await this.axios.post(api, data);
                    if (res.data.code == 1) {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'success'
                        });
                        this.dialogVisible = false;
                    } else {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'error'
                        });
                    }
                    this.getList();
                    this.getHash();
                }
            })
        },
        //关闭弹窗
        handleClose() {
            this.dialogVisible = false;
            // Object.assign(this.$data, this.$options.data())
        },
        //开始播放-停止播放
        async stop(row, status) {
            const res = await this.axios.post(temporaryRingtoneStatus, { id: row.id, state: status, hash: this.hash });
            if (res.data.code == 1) {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'success'
                });
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
            this.getList();
            this.getHash();
        },
        delBtn(id) {
            this.$confirm('是否确定删除该铃声任务', '删除提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            })
                .then(() => {
                    const datas = {
                        id: id,
                        hash: this.hash,
                    };
                    const res = this.axios.post(temporaryRingtoneDel, datas)
                        .then(res => {
                            if (res.data.code == 1) {
                                this.$notify.success({
                                    showClose: true,
                                    message: res.data.msg,
                                    type: 'success'
                                });
                                if ((this.total - 1) % this.pageSize === 0 && this.total > 1) {
                                    this.page -= 1;
                                }
                                this.getList();
                            } else {
                                this.$notify.error({
                                    title: '失败',
                                    message: res.data.msg
                                });
                            }
                        })
                        .catch(err => {
                            this.$notify.error({
                                title: '失败',
                                message: '取消操作'
                            });
                        });
                    this.getHash();
                })

        },
    }
};
</script>

<style lang="scss" scoped>
/deep/ .temporaryRingtone .ql-container .ql-editor {
    height: 280px;
}
.temporaryRingtone {
    .ql-editor {
        padding: 0;
    }
    .searchA {
        display: flex;
        padding: 12px 25px;
        background: #fff;
        .el-select {
            margin-right: 24px;
        }
        .el-input {
            width: auto;
            margin-right: 24px;
        }
        .add {
            flex: 1;
            text-align: right;
        }
    }

    .dialogInput {
        display: flex;
        margin-bottom: 0px;
        .padd {
            width: 100%;
            margin-right: 20px;
            .paddTit {
                margin-bottom: 10px;
            }
        }
    }
    .btn {
        width: 250px;
    }
    // /deep/ .el-table--enable-row-transition .el-table__body td.el-table__cell {
    //     padding: 0 !important;
    // }
}
</style>



